<template>
  <div class="page-home">


    <div class="header text-left">
      <div class="float-right">
        <button class="btn btn-sm btn-primary mr-4" v-on:click="reminder(null)">Crear recordatorio</button>
        <button class="btn btn-sm btn-primary" v-on:click="logout">Cerrar sesión</button>
      </div>
      <h2>
        BLACKBOZZ<template v-if="project.id"> - [{{project.name}}]</template>
      </h2>
    </div>



    <div class="row content">
      <div class="col-md-3" v-show="!small">

        <div class="reminders">
          <h2>Ver recordatorios</h2>
        </div>
        
        <div class="table-padded">
          <table>
            <thead>
              <tr>
                <th>Proyecto</th>
                <th>Acción</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="(item, index) in projects" :key="index">
                <td>{{item.name}}</td>
                <td>
                  <button class="btn btn-primary btn-sm" v-on:click="setProject(item)">Ver</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

      </div>
      <div :class="small ? 'col-12' : 'col-md-9'">
        

        <div class="" v-if="project.id">

          <div class="float-right">
            <button class="btn btn-primary btn-lg mr-2" @click="refresh">
              <i class="material-icons">refresh</i>
            </button>

            <button class="btn btn-primary btn-lg mr-2" @click="small = !small">
              <template v-if="!small">
                <i class="material-icons">fullscreen</i>  
              </template>
              <template v-else>
                <i class="material-icons">fullscreen_exit</i>
              </template>
            </button>
            <button class="btn btn-primary btn-lg" @click="$refs.createRequest.show()">Montar solicitud</button>
          </div>

        <div class="table-padded">
          <table>
            <thead>
              <tr>
                <th>ID</th>
                <th>%</th>
                <th>Por</th>

                <th>Actividad</th>
                <th v-show="small">Descripción</th>
                <th>Encargado</th>
                <th>Estado</th>
                <th>Prioridad</th>
                <th v-show="small">F. creación</th>
                <th v-show="small">F. entrega</th>

                <th>Acción</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="(item, index) in requests" :key="index" :class="{'inactive': item.status == 2 || item.status == 3}">
                <td>{{item.id}}</td>
                <td>{{item.percent}}%</td>

                <td>
                  <img v-on:click="showProfile(item.User)" :src="item.User.image" class="people" :alt="item.User.name" :title="item.User.name">
                </td>
                <td class="text-left">
                  <div class="text-truncate" style="max-width: 150px;">{{item.name}}</div>
                </td>

                <td v-show="small">
                  {{item.description}}
                </td>


                <td class="text-left">
                  <img v-for="(person, k) in item.people" :key="k" :src="person.image"
                  v-on:click="showProfile(person)" class="people">

                  <div class="people" v-on:click="addPerson(item)" v-if="user.userType == 1">+</div>
                </td>

                 
                <td>
                  
                  <template v-if="item.status == 2">
                    Finalizado
                  </template>
                  <template v-else-if="item.status == 1">
                    En proceso
                  </template>
                  <template v-else-if="item.status == 3">
                    Rechazada
                  </template>
                  <template v-else>
                    Pendiente
                  </template>
                </td>
                <td>
                  <template v-if="item.priority < 2">
                    <span class="priority high">Alta</span>
                  </template>

                  <template v-else-if="item.priority < 5">
                    <span class="priority medium">Media</span>
                  </template>

                  <template v-else>
                    <span class="priority low">Baja</span>
                  </template>
                </td>
                <td v-show="small">
                  {{item.createdAt}}
                </td>
                <td v-show="small">
                  {{item.date}}
                </td>
                <td>
                  <template v-if="user.userType <= 2 ">
                    <button class="btn btn-icon btn-sm mr-2" v-on:click="edit(item)">
                      <i class="material-icons">edit</i>
                    </button>
                  </template>

                  <button class="btn btn-icon btn-sm mr-2" v-on:click="reminder(item)">
                    <i class="material-icons">notifications</i>
                  </button>
                  <template v-if="user.userType <= 2 || user.id == item.User.id">
                    <button class="btn btn-icon btn-sm" v-on:click="showLogs(item)">
                      <i class="material-icons">library_books</i>
                    </button>
                  </template>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div v-else>
        <h2>Escoge un proyecto para ver el dashboard</h2>
      </div>
      </div>
    </div>

    <b-modal ref="createRequest" id="createRequest" hide-footer hide-header centered>
      
      <form v-on:submit.prevent="create">
        <div class="form-group">
          <label>Codigo dependiente</label>
          <input type="number" class="form-control" v-model="form.requestId">
        </div>

        <div class="form-group">
          <label>Nombre</label>
          <input type="text" class="form-control" v-model="form.name">
        </div>

        <div class="form-group">
          <label>Descripción</label>
          <textarea class="form-control" v-model="form.description"></textarea>
        </div>

        <div class="form-group">
          <label>Tipo</label>
          <select class="form-control" v-model="form.typeId">
            <option :value="undefined">Seleccione una opción</option>
            <option value="1">Nuevo requerimiento de desarrollo de software</option>
            <option value="2">Modificación o mejora</option>
            <option value="3">Corrección</option>
            <option value="4">Nuevo requerimiento infraestructura tecnológica</option>
            <option value="5">Entrega/suministro de información</option>
          </select>
        </div>

        <div class="form-group">
          <label>Tecnologias</label>
          <input type="text" class="form-control" v-model="form.techs">
        </div>


        <div class="form-group">
          <label>Prioridad</label>
          <select class="form-control" v-model="form.priority">
            <option :value="undefined">Seleccione una opción</option>
            <option value="1">Alta</option>
            <option value="2">Media</option>
            <option value="3">Medio-Baja</option>
            <option value="4">Baja</option>
          </select>
        </div>

        <button class="btn btn-primary">Crear solicitud</button>
  
      </form>
    </b-modal> 

    <b-modal ref="profileModal" id="profileModal" hide-footer hide-header centered size="lg">
      <img :src="profile.image" class="avatar">
      <h2>{{profile.name}}</h2>

      <template v-if="user.userType == 1">
        <ul class="list-group">
          <li v-for="(item, index) in profile.worked" class="list-group-item">DIA: <strong>{{item.day}}</strong>, HORAS: <strong>{{Math.floor(item.worked / 60)}}</strong></li>
        </ul>
      </template>

    </b-modal>

    <b-modal ref="logsModal" id="logsModal" hide-footer hide-header centered size="lg">
      <button class="btn btn-primary" v-on:click="comment">Crear seguimiento</button>
     
      

      <div class="table-padded">
          <table>
            <thead>
              <tr>
                <th>ID</th>
                <th>Comentario</th>
                <th>Fecha</th>
                <th>Persona</th>
                <th>Tiempo</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="(item, index) in logs" :key="index">
                <td>{{item.id}}</td>
                <td>{{item.comment}}</td>
                <td>{{item.createdAt}}</td>
                <td>{{item.userName}}</td>
                <td>
                  <template v-if="item.time">
                    {{item.time}}m
                  </template>
                  <template v-else> N/A </template>
                </td>
                
              </tr>
            </tbody>
          </table>
        </div>

    </b-modal> 

    <b-modal ref="createLogModal" id="createLogModal" hide-footer hide-header centered>
      <form v-on:submit.prevent="createLog">
       <div class="form-group">
          <label>Comentario</label>
          <textarea class="form-control" v-model="formComment.comment"></textarea>
       </div>

       <div class="form-group">
          <label>Minutos invertidos (Solo si aplica)</label>
          <input type="number" v-model="formComment.time" class="form-control">
       </div>

       <button class="btn btn-primary">Comentar</button>
      </form>
    </b-modal> 


    <b-modal ref="assignModal" id="assignModal" hide-footer hide-header centered>
      <form v-on:submit.prevent="assign">
       <div class="form-group">
          <label>Persona</label>
          <select class="form-control" v-model="formAssign.userId">
            <option v-for="(u, k) in users" :key="k" :value="u.id">{{u.name}}</option>
          </select>
       </div>

       <button class="btn btn-primary">Agregar</button>
      </form>
    </b-modal>



    <b-modal ref="editModal" id="editModal" hide-footer hide-header centered>
      <form v-on:submit.prevent="update">
       <div class="form-group">
          <label>Estado</label>
          <select class="form-control" v-model="form.status">
            <option value="0">Pendiente</option>
            <option value="1">En proceso</option>
            <option value="2">Finalizado</option>
            <option value="3">Rechazada</option>
          </select>
       </div>

       <div class="form-group">
          <label>Porcentaje</label>
          <input type="number" class="form-control" v-model="form.percent">
       </div>


       <div class="form-group">
          <label>Fecha de entrega</label>
          <input type="date" class="form-control" v-model="form.date">
       </div>

       <button class="btn btn-primary">Agregar</button>
      </form>
    </b-modal> 


    <b-modal ref="createReminderModal" id="createReminderModal" hide-footer hide-header centered>
      <form v-on:submit.prevent="reminderAction">

       <div class="form-group">
          <label>Recordatorio</label>
          <input type="text" class="form-control" v-model="formReminder.activity">
       </div>

       <div class="form-group">
          <label>Fecha</label>
          <input type="date" class="form-control" v-model="formReminder.date">
       </div>

       <button class="btn btn-primary btn-lg">Recuerdame</button>
      </form>
    </b-modal> 

  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {
  FETCH_PROJECTS, 
  FETCH_REQUESTS, 
  CREATE_REQUEST, 
  FETCH_LOGS, 
  PUT_LOG, 
  FETCH_USERS, 
  ASSIGN, 
  UPDATE_REQUEST, 
  REMINDER,
  DATA_USER
} from "@/store/actions.type";

export default {
  data() {
    return {
      project: {},
      profile: {},
      request: {},
      form: {
        projectId: null
      },
      formComment: {},
      formAssign: {},
      formReminder: {},
      small: false,
      loading: false
    };
  },
  mounted() {
    this.readUsers();
    this.read();
  },
  methods: {
    read() {
      this.$store.dispatch(FETCH_PROJECTS, 1);
    },
    refresh() {
      this.read();
      this.$snotify.success("Ok!");
    },
    readReqs() {
      this.$store.dispatch(FETCH_REQUESTS, this.project.id);
    },
    readUsers() {
      this.$store.dispatch(FETCH_USERS);
    },
    logout() {
      this.$router.replace({ name: "login" });
    },

    showProfile(profile) {
      this.profile = profile;
      this.$refs.profileModal.show();
      this.$store.dispatch(DATA_USER, this.profile.id).then(result => {
        this.$set(this.profile, "worked", result);
      });
    },

    create() {
      if (this.loading) return;
      this.loading = true;
      this.form.projectId = this.project.id;
      this.$store.dispatch(CREATE_REQUEST, this.form).then(result => {
        this.loading = false;
        this.$snotify.success(result.message);
        this.form = {};
        this.readReqs();
        this.$refs.createRequest.hide();
      })
      .catch(error => {
        this.loading = false;
        this.$snotify.error(error.message);
      });
    },

    setProject(data) {
      this.project = data;
      this.readReqs();
    },
    showLogs(item) {
      this.request = item;
      this.$store.dispatch(FETCH_LOGS, item.id);
      this.$refs.logsModal.show();
    },
    comment() {
      this.$refs.createLogModal.show();
    },
    createLog() {
      if (this.loading) return;
      this.loading = true;
      this.formComment.requestId = this.request.id;
      this.$store.dispatch(PUT_LOG, this.formComment)
        .then(result => {
          this.loading = false;
          this.$snotify.success(result.message);
          this.formComment = {};
          this.$refs.createLogModal.hide();
        })
        .catch(error => {
          this.loading = false;
          this.$snotify.error(error.message);
        });
    },

    addPerson(request) {
      this.request = request;
      this.$refs.assignModal.show();
    },
    assign() {
      if (this.loading) return;
      this.loading = true;
      this.formAssign.requestId = this.request.id;
      this.$store.dispatch(ASSIGN, this.formAssign)
        .then(result => {
          this.loading = false;
          this.$snotify.success(result.message);
          this.formAssign = {};
          this.$refs.assignModal.hide();
          this.readReqs();
        })
        .catch(error => {
          this.loading = false;
          this.$snotify.error(error.message);
        });
    },

    edit(request) {
      this.request = request;
      this.$refs.editModal.show();
    },

    update(request) {
      if (this.loading) return;
      this.loading = true;
      this.form.requestId = this.request.id;
      this.$store.dispatch(UPDATE_REQUEST, this.form)
        .then(result => {
          this.loading = false;
          this.$snotify.success(result.message);
          this.form = {};
          this.$refs.editModal.hide();
          this.readReqs();
        })
        .catch(error => {
          this.loading = false;
          this.$snotify.error(error.message);
        });
    },
    reminder(request) {
      this.request = request;
      this.$refs.createReminderModal.show();
    },
    reminderAction() {
      if (this.loading) return;
      this.loading = true;
      if (this.request) this.formReminder.requestId = this.request.id;
      this.$store.dispatch(REMINDER, this.formReminder)
        .then(result => {
          this.loading = false;
          this.$snotify.success(result.message);
          this.formReminder = {};
          this.$refs.createReminderModal.hide();
        })
        .catch(error => {
          this.loading = false;
          this.$snotify.error(error.message);
        });
    }
  },
  computed: {
    ...mapGetters(["projects", "requests", "logs", "user", "users"])
  }
}
</script>
<style lang="scss">
.page-home {

  background: #f4f8f9;
  min-height: 100vh;

  .header {
    background: #fff;
    min-height: 60px;
    border-bottom: 1px solid #ddd;
    padding: 20px;
  }

  .content {
    padding: 20px;

    .people {
      border: 1px solid #eee;
      padding: 3px;
      border-radius: 50px;
      min-width: 50px;
      width: 50px;
      height: 50px;
      margin-left: -10px;
      font-size: 28px;
      text-align: center;
      color: #aaa;
      background: #eee;
      cursor: pointer;
      display: inline-block;
      vertical-align: top;
    }
  }

  .priority {
    background: #dc3545;
    border-radius: 5px;
    padding: 3px;
    color: white;
    text-transform: uppercase;
    
    &.low {
      background: #495057;
    }
    
    &.medium {
      background: orange;
    }
  }

  .reminders {
    background: #4da0ff;
    color: white;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
    cursor: pointer;
    h2 {
      font-size: 16px;
      line-height: 1;
      margin: 0;
    }
  }

  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 200px;
  }
  .btn-icon {
    padding: 0;
    color: #4c9fff;
  }
}
</style>
