import ApiService from "@/common/api.service";
import {
  FETCH_PROJECTS,
  FETCH_REQUESTS,
  CREATE_REQUEST,
  FETCH_LOGS,
  PUT_LOG,
  ASSIGN,
  UPDATE_REQUEST,
  REMINDER
} from "../actions.type";
import { SET_PROJECTS, SET_REQUESTS, SET_LOGS } from "../mutations.type";

const state = {
  projects: [],
  requests: [],
  logs: []
};

const getters = {
  projects(state) {
    return state.projects;
  },
  requests(state) {
    return state.requests;
  },
  logs(state) {
    return state.logs;
  }
};

const mutations = {
  [SET_PROJECTS](context, data) {
    state.projects = data;
  },
  [SET_REQUESTS](context, data) {
    state.requests = data;
  },
  [SET_LOGS](context, data) {
    state.logs = data;
  }
};

const actions = {
  [FETCH_PROJECTS](context, companyId) {
    return ApiService.get("/request/projects/" + companyId).then(projects => {
      context.commit(SET_PROJECTS, projects);
      return Promise.resolve(projects);
    });
  },

  [FETCH_REQUESTS](context, projectId) {
    return ApiService.get("/request/request/" + projectId).then(request => {
      context.commit(SET_REQUESTS, request);
      return Promise.resolve(request);
    });
  },

  [FETCH_LOGS](context, requestId) {
    return ApiService.get("/request/logs/" + requestId).then(logs => {
      context.commit(SET_LOGS, logs);
      return Promise.resolve(logs);
    });
  },

  [PUT_LOG](context, data) {
    return ApiService.post("/request/comment", data).then(result => {
      return Promise.resolve(result);
    });
  },

  [CREATE_REQUEST](context, data) {
    return ApiService.post("/request/create", data).then(result => {
      return Promise.resolve(result);
    });
  },

  [ASSIGN](context, data) {
    return ApiService.post("/request/assign", data).then(result => {
      return Promise.resolve(result);
    });
  },

  [UPDATE_REQUEST](context, data) {
    return ApiService.post("/request/update", data).then(result => {
      return Promise.resolve(result);
    });
  },

  [REMINDER](context, data) {
    return ApiService.post("/request/reminder", data).then(result => {
      return Promise.resolve(result);
    });
  }
  
};

export default {
  state,
  getters,
  mutations,
  actions
};
