<template>
  <div class="login-page">
    <div class="bg"></div>
    <div class="content">
      <div class="box">
        <h2>BLACKBOZZ</h2>

        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
