        proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>

        <template v-if="step == 'home'">
            <button class="btn btn-p btn-lg" v-on:click="step = 'login'">Iniciar sesión</button>
          <div>
           <button class="btn btn-s btn-lg">Acerca de BlackBozz</button>
          </div>
        </template>
        <template v-else-if="step == 'login'">
          <form v-on:submit.prevent="login">
            <div>
              <input type="text" class="field-input" placeholder="Username" v-model="form.username">
            </div>

            <div>
              <input type="password" class="field-input" placeholder="Password" v-model="form.password">
            </div>
            <button class="btn btn-p btn-lg" v-on:click="step = 'login'">Iniciar sesión</button>
          </form>
        </template>
        
        
      </div>
    </div>
  </div>
</template>

<script>
import { LOGIN } from "@/store/actions.type";
export default {
  data: function() {
    return {
      step: "home",
      form: {}
    };
  },

  methods: {
    login() {
      this.$store
        .dispatch(LOGIN, this.form)
        .then(result => {
          this.$router.replace({ name: "home" });
        })
        .catch(error => {
          this.$snotify.error(error.message);
        })
    }
  }
}
</script>
